@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-v24-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/montserrat-v24-latin_cyrillic-regular.woff") format("woff");
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v24-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/montserrat-v24-latin_cyrillic-500.woff") format("woff");
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-v24-latin_cyrillic-700.woff2") format("woff2") url("../fonts/montserrat-v24-latin_cyrillic-700.woff") format("woff");
}

* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  min-width: 375px;
}

.container {
  max-width: 1380px;
  min-width: 375px;
  margin: 0 auto;
  padding: 0 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.button {
  border: none;
  background-color: transparent;
  padding: 0;
  text-decoration: none;
  display: inline-block;
}

.header {
  display: grid;
  grid-template-rows: min-content min-content;
  grid-template-columns: 1fr auto;
  margin: 0 -15px;
}

.header-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-column: span 2;
  padding: 12px 15px 13px;
}

.header-top__lang {
  font-size: 0;
}

.header-top__lang a {
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 11px;
  line-height: 15px;
  text-align: center;
  letter-spacing: 1px;
  color: #FFFFFF;
  padding: 5px 8px;
}

.header-top__lang a:hover {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
}

.header-top__addresses {
  display: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #FFFFFF;
}

.header-top__tel {
  font-weight: 700;
  font-size: 16.5px;
  line-height: 20px;
  color: #FFFFFF;
  text-decoration: none;
}

.header-top__fb {
  display: none;
}

.header-logo {
  margin-top: 15px;
  margin-bottom: 10px;
}

.header-logo__img {
  height: 32px;
  width: 69.3px;
}

.header-menu {
  display: flex;
}

.header-menu__list {
  display: none;
  list-style-type: none;
}

.header-menu__list a {
  margin-right: 15px;
  text-decoration: none;
  display: flex;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #FFFFFF;
}

@media screen and (min-width: 708px) {
  .header-top__fb {
    display: block;
  }
  .header {
    margin: 0;
    display: grid;
    grid-template-rows: repeat(3, auto);
    grid-template-columns: 1fr;
  }
  .header-top {
    display: flex;
    justify-content: flex-start;
    grid-column: 1 / -1;
    padding: 48px 0 30px;
  }
  .header-top__addresses {
    display: block;
  }
  .header-top__addresses::after {
    content: "";
    display: inline-block;
    vertical-align: middle;
    background: #E6E6F0;
    border-radius: 50%;
    width: 6px;
    height: 6px;
    margin: 0 6px;
  }
  .header-top__addresses::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    background: #E6E6F0;
    border-radius: 50%;
    width: 6px;
    height: 6px;
    margin: 0 6px;
  }
  .header-top__tel::after {
    content: "";
    display: inline-block;
    vertical-align: middle;
    background: #E6E6F0;
    border-radius: 50%;
    width: 6px;
    height: 6px;
    margin: 0 6px;
  }
  .header-menu__btn {
    display: none;
  }
  .header-menu__list {
    display: flex;
    padding-inline-start: 0;
  }
  .header-logo {
    margin-top: 0;
    margin-bottom: 25px;
  }
  .header-logo__img {
    width: 130px;
    height: 60px;
  }
}

@media screen and (min-width: 850px) {
  .header {
    display: grid;
    grid-template-rows: repeat(2, min-content);
    grid-template-columns: 1fr auto;
    grid-row-gap: 10px;
    padding-top: 50px;
  }
  .header-top {
    grid-row: 1 / 2;
    grid-column: 2 / -1;
    padding: 0;
  }
  .header-logo {
    grid-row: 1 / -1;
    grid-column: 1 / 2;
    align-self: center;
    margin: 0;
  }
  .header-logo__img {
    height: 65px;
    width: 130px;
  }
  .header-menu {
    justify-content: flex-end;
  }
  .header-menu__list a {
    margin-right: 0;
    margin-left: 15px;
  }
}

@media screen and (min-width: 992px) {
  .header {
    display: grid;
    grid-template-rows: repeat(2, min-content);
    grid-template-columns: 1fr auto;
    margin: 0;
    padding-top: 50px;
  }
}

.fon {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  border-radius: 0px 0px 12px 12px;
  position: relative;
}

.fon::after {
  content: '';
  background: #CACADB;
  border-radius: 0px 0px 12px 12px;
  height: 10px;
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: -10px;
}

.fon::before {
  content: '';
  background: #E7E7F2;
  border-radius: 0px 0px 12px 12px;
  height: 20px;
  position: absolute;
  left: 40px;
  right: 40px;
  bottom: -20px;
}

.fon__img {
  background-image: url(../img/h.svg);
  background-position-x: center;
  background-repeat: no-repeat;
  height: 600px;
  background-size: cover;
  background-position: center top;
}

@media screen and (min-width: 708px) {
  .fon__img {
    height: 1024px;
  }
}

@media screen and (min-width: 850px) {
  .fon__img {
    height: 768px;
    background-size: 69.5%;
  }
}

@media screen and (min-width: 992px) {
  .fon__img {
    background-size: 51%;
    height: 799px;
  }
}

.information {
  margin-top: 126px;
  margin-bottom: 30px;
}

.information__text {
  font-weight: 700;
  font-size: 38px;
  line-height: 46px;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 159px;
}

.information__link {
  display: block;
  text-align: center;
}

@media screen and (min-width: 708px) {
