.menu-nav {
  margin-right: -30px;
  display: flex;
  overflow: auto;
  margin-top: 30px;
  margin-bottom: 15px;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.menu-nav::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.menu-nav__btn {
  font-weight: 700;
  font-size: 32px;
  line-height: 37px;
  color: #B3B3BA;
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  background-color: transparent;
  white-space: nowrap;
  margin-right: 30px;
}

.menu-nav__btn:last-child {
  margin-right: 0;
}

.menu-information {
  border-top: 4px solid rgba(255, 255, 255, 0.3);
  padding: 30px 0;
}

.menu-information__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 24px;
  color: #FFFFFF;
  padding-bottom: 30px;
}

.menu-information__text {
  font-weight: 400;
  font-size: 19px;
  line-height: 30px;
  color: #FFFFFF;
}

@media screen and (min-width: 708px) {
  .menu-nav {
    margin-right: -30px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .menu-nav__btn {
    font-size: 41px;
    line-height: 46px;
  }
  .menu-information {
    padding: 60px 0;
  }
  .menu-information__title {
    font-size: 31px;
    line-height: 37px;
  }
}

@media screen and (min-width: 850px) {
  .menu {
    display: flex;
    padding-top: 60px;
    margin-bottom: 60px;
  }
  .menu-nav {
    width: 48%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-right: 60px;
    margin: 0;
    padding-top: 19px;
    padding-bottom: 0;
  }
  .menu-nav__btn {
    margin: 0;
    margin-bottom: 30px;
  }
  .menu-nav__btn:last-child {
    margin-bottom: 15px;
  }
  .menu-information {
    width: 52%;
    padding-left: 60px;
    border-left: 4px solid rgba(255, 255, 255, 0.3);
    border-top: none;
    padding-bottom: 0;
    padding-top: 13px;
  }
}

@media screen and (min-width: 992px) {
  .menu-nav {
    width: 42%;
  }
  .menu-nav__btn {
    font-size: 38px;
    line-height: 46px;
  }
  .menu-information {
    width: 58%;
    padding-top: 46px;
  }
  .menu-information__text {
    width: 83%;
  }
}

.services {
