@media screen and (min-width: 708px) {
  .services {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(5, min-content);
    grid-column-gap: 60px;
    padding-bottom: 0;
  }
  .services__link {
