  }
}

@media screen and (min-width: 850px) {
  .services {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: repeat(4, min-content);
  }
  .services__link {
    grid-column: 1 / 4;
  }
  .services__title {
    grid-column: 1 / 4;
  }
}

@media screen and (min-width: 992px) {
